	module.exports = {"data":{"markdownRemark":{"html":"<p>The ‘push’ method adds one or more elements to the end of an array and returns the new length of the array.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">var</span> colors <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'Red'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Green'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Purple'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\ncolors<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span><span class=\"token string\">'Yellow'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Pink'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>colors<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token comment\" spellcheck=\"true\">// results in ['Red', 'Green', 'Purple', 'Yellow', 'Pink'];</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/push\">MDN</a></p>","fields":{"slug":"/javascript/standard-objects/array/array-prototype-push/"},"frontmatter":{"title":"Array.prototype.push"}}},"pathContext":{"slug":"/javascript/standard-objects/array/array-prototype-push/"}}

